import { makeStyles, Typography, Button, Hidden } from '@material-ui/core'
import React from 'react'
import { Navbar, Nav, Container } from 'react-bootstrap'

const useStyles = makeStyles(theme => ({


    title: {
        flexGrow: 1,
        padding: theme.spacing(3, 3, 3, 10),
    },

}))

export default function Navbarr() {
    const classes = useStyles()
    return (

        <Navbar collapseOnSelect expand="xl" bg="black" variant="dark">
            <Container fluid>
                
                <Navbar.Brand href="#home">
                <Hidden mdDown>
                <Typography color='secondary' className={classes.title} variant='h3' >
                    Katz Entertainment
                </Typography>
                </Hidden>
                <Hidden lgUp smDown>
                <Typography color='secondary' className={classes.title} variant='h4' >
                    Katz Entertainment
                </Typography>
                </Hidden>
                <Hidden mdUp >
                <Typography color='secondary' className={classes.title} variant='h5' >
                    Katz Entertainment
                </Typography>
                </Hidden>
                    </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mx-auto me-5">
                        <Nav.Link href="#features"> <Button className={classes.navbutton} size='large' variant="outlined" >
                            Classes
                        </Button></Nav.Link>
                        <Nav.Link href="#features"> <Button className={classes.navbutton} size='large' variant="outlined" >
                            Contacts
                        </Button></Nav.Link>
                        <Nav.Link href="#features"> <Button className={classes.navbutton} size='large' variant="outlined" >
                            Products
                        </Button></Nav.Link>

                    </Nav>
                    <Nav className="ms-auto me-5">
                        <Nav.Link href="#features"> <Button className={classes.navbutton} color="secondary" variant="outlined" >
                            Bookigs
                        </Button></Nav.Link>
                        <Nav.Link href="#features"> <Button className={classes.navbutton} color="secondary" variant="outlined" >
                            Boobiekatz
                        </Button></Nav.Link>
                        <Nav.Link href="#features"> <Button className={classes.navbutton} color="secondary" variant="outlined" >
                            Katz2
                        </Button></Nav.Link>

                    </Nav>

                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

